<template>
  <v-navigation-drawer
    app
    v-model="DRAWER_STATE"
    :permanent="$vuetify.breakpoint.mdAndUp"
    :width="sidebarWidth"
    :temporary="$vuetify.breakpoint.smAndDown"
  >
    <v-list-item>
      <v-list-item-content>
        <v-list-item-title class="text-h6"> Market Research </v-list-item-title>
        <v-list-item-subtitle> My Workspace </v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
    <v-list nav dens>
      <template v-for="(item, i) in items">
        <v-divider v-if="item.divider" :key="i" dark class="my-4"></v-divider>

        <v-list-item
          color="primary"
          v-else
          :key="item.text"
          :href="item.href ? item.href : null"
          :to="item.link === '#' ? null : item.link"
          link
        >
          <v-list-item-action>
            <v-icon :color="item.color ? item.color : ''">{{
              item.icon
            }}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title
              style="font-size: 0.825rem"
              class="font-weight-medium"
              link
            >
              {{ item.title }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import { mapActions, mapState } from 'vuex';

export default {
  props: {
    source: String,
  },
  data() {
    return {
      selectedItem: 1,
      items: [
        { title: 'Dashboard', icon: 'mdi-home', link: '/dashboard' },
        // { title: 'Typography', icon: 'mdi-format-size', link: '/typography' },
        // { title: 'Tables', icon: 'mdi-grid-large', link: '/tables' },
        // {
        //   title: 'Notifications',
        //   icon: 'mdi-bell-outline',
        //   link: '/notifications',
        // },
        // {
        //   title: 'UI Elements',
        //   icon: 'mdi-image-filter-none',
        //   link: '/icons',
        //   model: false,
        //   children: [
        //     { title: 'Icons', icon: 'mdi-circle-small', link: '/icons'},
        //     { title: 'Charts', icon: 'mdi-circle-small', link: '/charts'},
        //     { title: 'Maps', icon: 'mdi-circle-small', link: '/maps'},
        //   ],
        // },
      ],
      sidebarWidth: 240,
      sidebarMinWidth: 80,
    };
  },
  computed: {
    ...mapState(['drawer']),
    DRAWER_STATE: {
      get() {
        return this.drawer;
      },
      set(newValue) {
        if (newValue === this.drawer) return;
        this.TOGGLE_DRAWER();
      },
    },
  },
  methods: {
    ...mapActions(['TOGGLE_DRAWER']),
  },
};
</script>

