<template>
  <v-container fluid>
    <div class="dashboard-page">
      <v-row no-gutters class="d-flex justify-space-between mt-10 mb-6">
        <h1 class="text-h6 page-title">Response Summary</h1>
        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <vue-json-to-csv
              v-bind="attrs"
              v-on="on"
              :json-data="tabledata"
              csv-title="pmf_survey"
            >
              <v-btn color="primary" class="text-capitalize button-shadow mr-1"
                >Download CSV</v-btn
              >
            </vue-json-to-csv>
          </template>
        </v-menu>
      </v-row>
      <v-row>
        <v-col lg="12" sm="12" cols="12">
          <v-card class="mx-1 mb-1">
            <v-data-table
              :loading="tabledata.length > 0 ? false : true"
              :headers="headers"
              :items="tabledata"
              :items-per-page="8"
              item-key="name"
              class="elevation-1"
            >
              <template v-slot:[`item.question`]="{ item }">
                <span class="font-weight-bold">{{ item.question }}</span>
              </template>

              <template v-slot:[`item.response`]="{ item }">
                <v-chip :color="getColor(item.response)" dark>
                  {{ item.response }}
                </v-chip>
              </template>
              <template v-slot:[`item.date`]="{ item }">
                {{ getDate(item.date) }}
              </template>
            </v-data-table>
          </v-card>
          <template v-for="(item, index) in mapdata">
            <v-card v-if="(item[0] === 'How helpful are the video chapters for your navigation?')" class="mx-auto mb-16 mt-16" tile :key="index">
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title  class="text-h5">{{
                    item[0]
                  }}
                  <v-chip class="ma-2" color="default">{{ item[1].data.length }} response</v-chip>
                  
                  </v-list-item-title>
                </v-list-item-content>
              </v-list-item>

              <v-list-item>
                <v-list-item-content>
                  <div
                    v-for="(response, index) in item[1].response"
                    :key="index"
                  >
                    <v-progress-linear
                      :value="
                        percentage(
                          item[1].responseCount[index],
                          item[1].data.length
                        )
                      "
                      :color="responseColor(response)"
                      height="40"
                    >
                      <template v-slot:default="{ value }">
                        <div
                          class="d-flex justify-space-between px-6"
                          style="width: 100%"
                        >
                          <strong>{{ response }}</strong>
                          <strong>{{ Math.ceil(value) }}%</strong>
                          <strong
                            >{{ item[1].responseCount[index] }} response</strong
                          >
                        </div>
                      </template>
                    </v-progress-linear>

                    <br />
                  </div>
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </template>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import VueJsonToCsv from 'vue-json-to-csv';

export default {
  name: 'Results',
  components: {
    VueJsonToCsv,
  },
  data: () => ({
    headers: [
      {
        text: 'Id',
        align: 'start',
        value: 'questionId',
      },
      { text: 'Question', value: 'question' },
      { text: 'Response', value: 'response' },
      { text: 'Comment', value: 'comment' },
      { text: 'Date', value: 'date' },
    ],
    tabledata: [
      {
        questionId: '',
        question: '',
        response: '',
        comment: '',
        date: '',
      },
    ],
    mapdata: new Map(),
  }),
  methods: {
    percentage: function (givenResponse, totalReponse) {
      // console.log(givenResponse, totalReponse, Math.round(Math.abs(givenResponse / totalReponse) * 100));
      return Math.round(Math.abs(givenResponse / totalReponse) * 100)
    },
    responseColor: function (response) {
      if (
        response === 'Somewhat disappointed' ||
        response === 'Somewhat helpful'
      )
        return 'yellow';
      if (response === 'Not disappointed') return 'blue';
      if (response === 'Very disappointed' || response === 'Not really helpful')
        return 'red';
      if (response === 'Very helpful') return 'green';
      return 'blue-grey';
    },
    getAllFeedback: function () {
      fetch('https://ncme1c4ghi.execute-api.ap-south-1.amazonaws.com/feedback/')
        .then((response) => response.json())
        .then((data) => {
          // console.log(data);
          // var uniq = [...new Set(data.Items)];
          var map = new Map();
          const key = data.Items[0].question;
          map.set(key, {
            question: data.Items[0].question,
            data: [],
            response: [],
            responseCount: [],
          });

          for (var i = 0; data.Items.length > i; i++) {
            if (!map.get(data.Items[i].question)) {
              const key = data.Items[i].question;
              map.set(key, {
                question: data.Items[i].question,
                data: [],
                response: [],
                responseCount: [],
              });
            } else {
              map.get(data.Items[i].question).data.push(data.Items[i]);
              var response = map.get(data.Items[i].question).response;
              var responseCount = map.get(data.Items[i].question).responseCount;
              const responseIndex = response.indexOf(data.Items[i].response);

              if (
                data.Items[i].response &&
                response.indexOf(data.Items[i].response) === -1
              ) {
                response.push(data.Items[i].response);
                responseCount[response.length - 1] = 1;
              } else {
                responseCount[responseIndex] = responseCount[responseIndex] + 1;
              }
            }
          }

          // console.log(map);

          this.mapdata = map;
          this.tabledata = [...data.Items];
        });
    },
    getColor(response) {
      if (response === 'Somewhat disappointed' || response === 'Somewhat helpful') return 'orange';
      if (response === 'Very disappointed' || response === 'Not really helpful') return 'red';
      if (response === 'Very helpful' || response === 'Not disappointed') return 'green';
      return 'blue-grey';
    },
    getDate(timestamp) {
      var date = new Date(timestamp);
      return (
        date.getDate() +
        '/' +
        (date.getMonth() + 1) +
        '/' +
        date.getFullYear() +
        ' ' +
        date.getHours() +
        ':' +
        date.getMinutes() +
        ':' +
        date.getSeconds()
      );
    },
  },
  mounted() {
    setTimeout(() => {
      this.apexLoading = true;
    });
  },
  created: function () {
    this.getAllFeedback();
  },
};
</script>

<style src="./Results.scss" lang="scss" />
